esContent":["\nbody {\n    font-family: 'Roboto', sans-serif !important;\n    font-weight: 300;\n\n    .dropdown-menu {\n        padding: var(--menu-container-padding);\n        li {\n            text-align: left;\n            display: flex;\n            flex-direction: row;\n            height: 32px;\n            align-items: center;\n            cursor: pointer;\n            border-radius: 4px;\n            padding-left: 4px;\n            padding-right: 4px;\n        }\n    }\n\n}\n  \n@keyframes spinner {\n    to {\n        transform: translate(-50%, -50%) rotate(360deg);\n    }\n}\n\n.spinner {\n    >*{\n        opacity: 0.08 !important;\n    }\n    &:before {\n        content: '';\n        transform: translate(-50%, -50%) ;\n        position: absolute;\n        z-index: 2000;\n        top: 50%;\n        left: 50%;\n        width: 30px;\n        height: 30px;\n        border-radius: 50%;\n        border-width:2px;\n        border-style: solid;\n        animation: spinner .6s linear infinite;\n    }\n}\n\n\n.tooltip{\n    z-index: 1000000;\n}\n  ","@import \"../../common/less/index\";\n\n@import \"variables\";\n@import \"hero\";\n@import \"intro\";\n@import \"slidedeck\";\n\n\nbody {\n    margin: 0;\n    padding: 0;\n    overflow-x: hidden;\n    overflow-y: scroll;\n    scroll-snap-type: y mandatory;\n}\n\n",".cookiebar{\n    position: fixed;\n    bottom: 0px;\n    background-color: rgb(var(--pane-bg-color));\n    z-index: 100000;\n    color:rgb(var(--text-color));\n    margin: 23px;\n    right: 0px;\n    left: 0px;\n    padding: 30px;\n    box-shadow: var(--box-shadow-2);\n    border-radius: @borderRadiusSmall;\n    display: flex;\n    align-items: center;\n    border:1px solid rgb(var(--tint-color));\n    h1{\n        flex: 1;\n        font-size: 1em;\n        font-weight: bold;\n    }\n}",".userinfo_toggler{\n\n  .userContainer {\n    text-align: center;\n    img {\n      width: 90px;\n    }\n  }\n}\n",".appbar {\n    height:@appbarHeight;\n    position: relative;\n    border: none !important;\n    display: flex;\n    align-items: center;\n    gap: 10px;\n    padding-left:10px;\n    padding-right:10px;\n\n    .icon {\n        height: @toolbarHeight - 20;\n    }\n    .title {\n        h1 {\n            font-size: 24px;\n            font-weight: 200;\n            letter-spacing: 6px;\n            margin: 0;\n        }\n        h2 {\n            font-size: 14px;\n            font-weight: 400;\n            letter-spacing:4px;\n            margin: 0;\n        }\n    }\n\n    .spacer {\n        flex-grow: 1;\n    }\n\n    .group {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n    }  \n    \n    .image-button{\n        cursor:pointer;\n    }\n}\n",".list {\n    padding: var(--menu-container-padding);\n    margin: 0;\n    border: 0;\n\n    .list-item {\n      font-weight: 400;\n      height: @buttonHeight;\n      font-size: var(--label-font-size);\n      padding: var(--menu-container-padding);\n      border-radius: @borderRadiusSmall;\n\n      &:hover{\n        background-color: rgb(var(--list-item-hover-bg-color));\n        .list-item-action {\n          opacity: 1; \n          transform: scale(1); \n          transition: all 0.2s ease;\n        }\n      }\n      &.selected {\n        background-color: rgb(var(--list-item-selected-bg-color));\n        .list-item-action {\n          opacity: 1; \n          transform: scale(1); \n          transition: all 0.2s ease;\n        }\n      }\n\n      .list-item-action {\n        opacity: 0; \n        transform: scale(0.5); \n        transition: all 0.2s ease;\n        font-size: 20px;\n        min-width: 25px;\n      }\n\n      &:before {\n        background-color: rgb(var(--tint-color));\n        block-size: 16px;\n        border-radius: 3px;\n        content: \"\";\n        inline-size: 3px;\n        inset-inline-start: 0;\n        opacity: 0;\n        position: absolute;\n        transform: scaleY(0);\n        transition: transform 170ms cubic-bezier(0, 0, 0, 1);\n        margin-left:2px;\n      }\n\n      &.selected {\n        &:before {\n          opacity: 1;\n          transform: scaleY(1);\n        }\n      }\n    }\n  }",".toolbar {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: @buttonSpacing;\n  padding-right: 10px;\n  height: @toolbarHeight;\n  padding: var(--menu-container-padding);\n\n  * {\n    outline: none;\n  }\n\n  .spacer {\n    flex-grow: 1;\n  }\n\n  .group {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height:100%;\n    gap: @buttonSpacing;\n\n    .statusIndicator {\n      margin: 20px;\n\n      img {\n        display: block;\n        cursor: pointer;\n        margin: auto;\n      }\n\n      span {\n        font-size: .8em;\n        text-align: center;\n        width: 100%;\n        display: inline-block;\n        white-space: nowrap;\n      }\n\n      .notSupported {\n        display: none;\n        font-weight: bold;\n      }\n\n      .connected {\n        display: block;\n      }\n\n      .disconnected {\n        display: none;\n      }\n    }\n\n    .statusIndicator.disabled {\n      img {\n        cursor: not-allowed;\n\n        &:hover {\n          box-shadow: none;\n        }\n      }\n\n      .notSupported {\n        display: block;\n      }\n\n      .connected {\n        display: none;\n      }\n\n      .disconnected {\n        display: none;\n      }\n    }\n\n    .statusIndicator.error:not(.disabled) {\n      span {\n        font-weight: bold;\n      }\n\n      .notSupported {\n        display: none;\n      }\n\n      .connected {\n        display: none;\n      }\n\n      .disconnected {\n        display: block;\n      }\n    }\n  }\n}",".tree-leaf {\n    font-weight: var(--label-font-weight);\n    font-size: var(--label-font-size);\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    float: unset;\n    min-height:28px;\n\n    .tree-leaf-content{\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      cursor: pointer;\n      transition: all 0.4s;\n      padding-left:3px;\n      height: 25px;\n      border-radius: @borderRadiusSmall;\n      &.tree-child-leaves{\n        transition: all 0.4s;\n      }\n      &:hover{\n        transition: all 0.4s;\n      }\n      &.selected{\n        transition: all 0.4s;\n      }\n      .tree-leaf-text{\n        float:unset;\n      }\n      .tree-expando {\n        float: unset;\n        top: unset;\n        position: unset;\n        width: 16px;\n        height: 16px;\n        line-height: 14px;\n        border-radius: @borderRadiusSmall;\n        border-width: 1px;\n        &.hidden{\n          display: block !important;\n          visibility: hidden !important;\n        }\n      }\n    }\n}\n","/** don't place \".section\" in the sub-tree of \".sections\" . The \".section\" is \n    used in other page elements like pdf, dialog, single page, ... as well without the\n    outer \"sections\" content\n **/ \n.section {\n  margin-left:20px;\n  margin-right:20px;\n  cursor: pointer;\n  border-width:1px;\n  border-style:solid;\n  padding-left:3px;\n  position: relative;\n\n\n  &.error {\n    border-width:1px;\n    border-style:solid;\n  }\n\n  .fc{\n    position: absolute;\n    top: 0px;\n    bottom: 0px;\n    right:0px;\n    width:5px;\n    .tinyFlyoverMenu {\n      opacity: 0;\n      z-index: unset;\n    }\n  }\n\n  &[data-type='timing'],\n  &[data-type='image']{\n    text-align: center;\n  }\n\n  .sectionContent {\n    /* required for flashcard */\n    perspective: 1800px;\n\n    \n    .placeholderContainer{\n      display: flex;\n      width: fit-content;\n      padding:15px;\n      border-radius:@borderRadiusBig;\n      margin: 50px;\n      margin-left: auto;\n      margin-right: auto;\n      border-style: dashed;\n      border-width: 1px;\n\n      .placeholderButtons{\n        display: flex;\n        flex-direction: column;\n        gap: 5px;\n        padding-left: 3vw;\n        justify-content: center;\n        .placeholderMenuInsertSection{\n          text-align: left;\n        }\n      }\n      .placeholderText{\n        h1{\n          font-size:14px !important;\n          font-weight: bold !important;\n          border:none !important;\n        }\n        h2{\n          font-size: var(--label-font-size);\n          font-weight:var(--label-font-weight);\n        }\n      }\n    }\n\n    &[data-type='image']{\n      text-align: center;\n      max-width: 100%;\n\n      #editor-container {\n        display: inline-grid;\n        min-height: 200px;\n        position: relative;\n        justify-items: center;\n        width:100%;\n        .drop-message{\n          height: 100px;\n          border: 1px solid rgb(var(--border-color));\n          display: flex;\n          width: 300px;\n          align-items: center;\n          justify-content: center;\n          background-color: rgba(var(--pane-bg-color),0.4);\n          border-radius: @borderRadiusBig;\n          box-shadow: var(--box-shadow-1);\n          grid-area: 1 / 1 / 2 / 2;\n          position: absolute;\n          margin-top: 2vh;\n          backdrop-filter: blur(4px);\n          font-size: var( --label-font-size);\n          font-weight: var( --label-font-weight);\n      \n        }\n        .scaleSlider {\n          position: absolute;\n          width:100%;         \n        }\n        #image-preview {\n          grid-area: 1 / 1 / 2 / 2;\n          width:100%;\n          .image-view {\n            display: inline-block;\n            position: relative;\n            margin-right: 13px;\n            margin-bottom: 13px;\n            width:100%;\n            img {\n              max-width: 100%;\n            }\n          }\n          .overlay {\n            position: absolute;\n            width: 100%;\n            height: 100%;\n            top: 0;\n            right: 0;\n          }\n        }\n      }\n    }\n\n    &[data-type='flashcard'],\n    &[data-type='wysiwyg'],\n    &[data-type='markdown'],\n    &[data-type='cloze']{\n      h1 {\n        font-weight: 400;\n        margin-bottom: 0;\n        margin-bottom: 18px;\n        margin-top: 19px;\n        border-bottom-width: 2px;\n        border-bottom-style: solid;\n        font-size: 18px;\n      }\n      h2, h3 {\n        font-weight: 300;\n        font-size: 18px;\n      }\n\n      table { \n        margin-left:auto !important;\n        margin-right:auto !important;\n\n        border: 1px solid rgb(var(--border-color));\n        border-collapse: separate;\n        border-left: 0;\n        border-radius: 4px;\n        border-spacing: 0px;\n        thead {\n          display: table-header-group;\n          vertical-align: middle;\n          border-color: inherit;\n          border-collapse: separate;\n        }\n        tr {\n            display: table-row;\n            vertical-align: inherit;\n            border-color: inherit;\n        }\n        th, td {\n            padding: 5px 8px 6px 8px; \n            text-align: left;\n            vertical-align: top;\n            border-left: 1px solid rgb(var(--border-color));    \n        }\n        td {\n            border-top: 1px solid rgb(var(--border-color));    \n        }\n        thead:first-child tr:first-child th:first-child, tbody:first-child tr:first-child td:first-child {\n            border-radius: 4px 0 0 0;\n        }\n        thead:last-child tr:last-child th:first-child, tbody:last-child tr:last-child td:first-child {\n            border-radius: 0 0 0 4px;\n        }\n        \n      }\n      \n\n      .info{\n        border-width:1px;\n        border-style: solid;\n        border-radius: @borderRadiusBig;\n        font-weight: 400;\n        letter-spacing: 2px;\n        padding: 5px;\n        padding: 5px;\n        padding-left: 20px;\n        padding-right: 20px;\n        p{\n          padding: 0;\n          margin:0;\n        }\n      }\n    }\n\n    &[data-type='brain']{\n      text-align:center;\n      position: relative;\n      m