   --workplace-bg-color: 245, 248, 255;\n\n    /* background color of a pane, dialog, document or everything which is place inside a workplaces lke a file-selector */\n    --pane-bg-color: 255, 255, 255;\n\n\n    color: rgb(var(--text-color));\n\n    .electra-button{\n        background-color: rgb(var(--tint-color));\n        color: white;\n        &:hover{\n            filter: brightness(110%);\n        }\n    }\n\n    a {\n        color: rgb(var(--tint-color));\n    }\n\n    /* class used by draw2d if a element is drag&drop from a platette into a workspace */\n    .shadow {\n        filter: drop-shadow(0 1px 3px rgba(0,0,0,0.16));\n    }\n\n    .dropdown-menu {\n        border: 1px solid rgb(var(--border-color));\n        border-radius: @borderRadiusSmall;\n        background-color: rgb(var(--pane-bg-color));\n        li {\n            color: rgb(var(--input-color));\n            &:hover{\n                background-color: rgb(var(--list-item-hover-bg-color));\n            }\n        }\n    }\n\n    input[type=text]{\n        color: rgb(var(--input-color));        \n        border: 1px solid rgb(var(--border-color));\n        &:focus {\n            background-color: white;\n            border-bottom: 2px solid rgb(var(--tint-color));\n        }\n    }\n\n    .notifyjs-bootstrap-base {\n        box-shadow: var(--box-shadow-1);\n    }\n    \n    .notifyjs-bootstrap-info {\n        padding: var(--menu-container-padding);\n        color: rgb(var(--text-color));\n        background-color: rgb(var(--pane-bg-color));\n    }\n\n    .appbar {\n        background-color:rgb(32, 43, 59);\n        .title {\n            h1 {\n                color: white;\n            }\n            h2 {\n                color: white;\n            }\n        }\n        .slogan{\n            color: white;\n            letter-spacing: 0.2vw;\n        }\n        .icon {\n            path,\n            rect {\n                fill:  rgb(var(--tint-color));\n                stroke:  rgb(var(--tint-color));\n            }\n        }\n\n        .image-button {\n            border: 1px solid transparent;\n\n            &:hover{\n                border: 1px solid white;\n            }\n            img,\n            svg {\n                color: white;\n                border-color:transparent;\n    \n                circle[stroke],\n                polyline[stroke],\n                path[stroke],\n                g[stroke] {\n                    stroke: white !important;\n                }\n    \n                rect[fill],\n                circle[fill] {\n                    fill: white !important;\n                }\n            }\n        }\n    }\n\n    .spinner:before {\n        border-color:  rgb(var(--border-color));\n        border-top-color: rgb(var(--tint-color));\n        background-color: #fef9f9;\n    }\n\n    .confirm-dialog-btn-confirm {\n        background-color: rgb(var(--tint-color));\n    }\n\n    .context-menu-list {\n        border-color: rgb(var(--border-color));\n        background: rgb(var(--pane-bg-color));\n        border-left-color: rgb(var(--tint-color));\n        box-shadow:  var(--box-shadow-1);\n\n        .context-menu-separator {\n            border-bottom: 1px solid rgb(var(--border-color));\n        }\n\n        .context-menu-item {\n            &.hover {\n                background-color: rgb(var(--tint-color));\n                color: white;\n            }\n\n            &.disabled {\n                color: rgba(var(--text-color), 0.5);\n\n                &.hover {\n                    background-color: #EEE;\n                }\n            }\n\n            .context-menu-submenu:after {\n                color: #666;\n            }\n        }\n\n    }\n\n    .gutter {\n        background-color: #eee;\n        background-repeat: no-repeat;\n        background-position: center;\n        &.gutter-vertical {\n            background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAFAQMAAABo7865AAAABlBMVEVHcEzMzMzyAv2sAAAAAXRSTlMAQObYZgAAABBJREFUeF5jOAMEEAIEEFwAn3kMwcB6I2AAAAAASUVORK5CYII=');\n        }\n\n        &.gutter-horizontal {\n            background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');\n        }\n    }\n\n    .tinyFlyoverMenu {\n        box-shadow:  var(--box-shadow-1);\n        border-color: rgb(var(--border-color));\n        background-color: rgb(var(--pane-bg-color));\n\n        div {\n            border-color: transparent;\n            &:hover {\n                border-color: rgb(var(--border-color));\n            }\n        }\n    }\n\n\n    #leftTabStrip {\n        background-color: rgb(var(--lefttab-bg-color)) ;\n\n        &:after {\n            color: rgb(var(--lefttab-font-color));\n        }\n\n        >li,\n        >div {\n            &.active {\n                a{\n                    &:hover {\n                        background-color: white;\n                    }\n\n                    svg {\n                        path[stroke],\n                        rect[stroke],\n                        g[stroke],\n                        line[stroke],\n                        circle[stroke],\n                        polyline[stroke] {\n                            stroke: rgb(var(--lefttab-bg-color))  !important;\n                        }\n\n                        circle[fill],\n                        rect[fill] {\n                            fill: rgb(var(--lefttab-bg-color))  !important;\n                        }\n                    }\n                }\n            }\n\n            a {\n                &:hover {\n                    background-color: rgba(0, 0, 0, 0.1);\n                }\n\n                svg {\n\n                    path[stroke],\n                    rect[stroke],\n                    g[stroke],\n                    line[stroke],\n                    circle[stroke],\n                    polyline[stroke] {\n                        stroke:rgb(var(--lefttab-font-color)) !important;\n                    }\n\n                    circle[fill],\n                    rect[fill] {\n                        fill: rgb(var(--lefttab-font-color))!important;\n                    }\n                }\n            }\n        }\n    }\n\n    #home {\n        .authorPage {\n            h1 {\n                color:rgb(var(--tint-color));\n            }\n\n            h2 {\n                color: rgb(var(--tint-color));\n            }\n        }\n\n        footer {\n            color:rgb(var(--tint-color));\n\n            a {\n                color: rgb(var(--tint-color));\n            }\n        }\n    }\n\n    #canvas_zoom {\n        background-color: rgb(var(--pane-bg-color));\n        border: 1px solid rgb(var(--border-color));\n        box-shadow: var(--box-shadow-0);\n        button {\n            background-color: transparent;\n            border-color: transparent;\n            transition: all 0.5s;\n\n            &:hover {\n                border-color:rgb(var(--tint-color));\n            }\n        }\n    }\n\n    #configMenuIcon {\n        &:hover {\n            opacity: 1;\n            color: rgb(var(--tint-color));\n        }\n    }\n\n    .modal-backdrop.in {\n        opacity: 0.7;\n        background-color: black;\n    }\n\n    .genericDialog {\n        .modal-content {\n            box-shadow:  var(--box-shadow-3);\n            background-color:rgb(var(--pane-bg-color));\n\n            .modal-header {\n                h4{\n                    border-bottom: 1px solid rgb(var(--tint-color));\n                    color: rgb(var(--tint-color));\n                }\n            }\n\n            .modal-body {\n                .list-group {\n\n                    *[data-draw2d=\"true\"] {\n                        color: rgb(var(--tint-color));\n                    }\n\n                    .list-group-item {\n                        background-color: transparent;\n                    }\n\n                    *[data-draw2d=\"false\"][data-type=\"file\"] {\n                        color: gray;\n                    }\n                }\n            }\n\n            .modal-footer {\n                background-color: transparent;\n            }\n        }\n    }\n\n    .tab-pane {\n        box-shadow: -6px 0 20px -4px rgba(31, 73, 125, 0.3);\n    }\n\n    #files {\n        background-color: rgb(var(--workplace-bg-color));\n        .teaser {\n            .title {\n                color: rgb(var(--tint-color));\n            }\n        }\n\n        .list-group-item {\n            cursor: pointer;\n            .thumb {\n                h4 {\n                    color:rgb(var(--tint-color));\n                }\n            }\n        }\n        .filesFinder {\n            background-color: rgb(var(--pane-bg-color));\n            border: 1px solid rgb(var(--border-color));\n            border-radius: @borderRadiusBig;\n            box-shadow: var(--box-shadow-1);\n            header{\n                #material-tabs {\n                    border: none;\n                    a {\n                        color: rgb(var(--input-label-color));\n                        &.active{\n                            color:rgb(var(--tint-color));\n                        }\n                        &:hover {\n                            color:rgb(var(--tint-color));\n                        }\n                    }\n                }\n            }\n\n            .material-tab-content {\n                .fileOperations{\n                    border-color: rgb(var(--border-color));\n                }\n            }\n\n            .sliding-bar {\n                background:transparent;\n                transition: all 0.3s ease;\n                border-color:rgb(var(--tint-color));\n            }\n        }\n    }\n\n    #figureConfigDialog{\n        textarea {\n            &.figureAttribute {\n                background-color:#272822;\n                border-color:#272822;\n                color:#ffffff;\n            }\n            &.lineNumbering {\n                background-color:#3E3D32;\n                border-color:#3E3D32;\n                color:#928869;\n            }\n        }\n    }\n  \n    #notificationToast {\n        background-color:rgb(var(--tint-color));\n        color: white;\n    }\n\n    .tree {\n        .tree-leaf {\n            \n\n            .tree-leaf-content {\n\n                .tree-expando {\n                    background-color: transparent;\n                    border: 1px solid rgb(var(--input-label-color));\n                    color: rgb(var(--input-label-color));\n\n                }\n                &:hover {\n                    background-color: rgb(var(--list-item-hover-bg-color));\n                }\n\n                &.selected {\n                    background-color: rgb(var(--list-item-selected-bg-color));\n                    .tree-leaf-text {\n                        color: rgb(var(--tint-color));\n                    }\n                    .tree-expando {\n                        color: rgb(var(--tint-color));\n                        border-color: rgb(var(--tint-color));\n                    }\n                }\n            }\n        }\n    }\n\n    /** don't place \".section\" in the sub-tree of \".sections\" . The \".section\" is \n        used in other page elements like pdf, dialog, single page, ...\n    **/ \n    .section {\n        border-color: transparent;\n        &.error {\n            border-color:rgb(var(--tint-color)) ;\n        }\n        .sectionContent {\n            &[data-type='flashcard'],\n            &[data-type='wysiwyg'],\n            &[data-type='markdown'],\n            &[data-type='cloze'] {\n                h1 ,\n                h2 ,\n                h3 {\n                    color: rgb(var(--tint-color)) ;\n                }\n                h1{\n                    border-bottom-color: rgb(var(--tint-color)) ;\n                }\n                .cloze {\n                    background-color: lightgray;\n                    padding-left: 15px;\n                    padding-right: 15px;\n                    border-radius: @borderRadiusSmall;\n                    border-bottom: 1px dotted gray;\n                }\n                a {\n                    color: inherit;\n                    text-decoration: none;\n                    background:\n                    linear-gradient(\n                        to right,\n                        rgb(var(--toolbar-bg-color)),\n                        rgb(var(--toolbar-bg-color))\n                    ),\n                    linear-gradient(\n                        to right,\n                        rgb(var(--tint-color)),\n                        rgba(255, 0, 180, 1),\n                        rgba(0, 100, 200, 1)\n                    );\n                    background-size: 100% 1px, 0 1px;\n                    background-position: 100% 100%, 0 100%;\n                    background-repeat: no-repeat;\n                    transition: background-size 200ms;\n                }    \n                a:hover {\n                    background-size: 0 1px, 100% 1px;\n                }\n            }\n\n            /** learning content gets a special annotation on the left **/\n            &[data-type=\"flashcard\"] {\n                .sectionMenuFlip {\n                    background-color: rgba(var(--tint-color), 0.2) ;\n                    border: 1px solid white;\n                }\n            }\n\n            .placeholderContainer{\n                border-color:rgb(var(--border-color));\n                .placeholderText{\n                h1{\n                    color: rgb(var(--text-color));\n                }\n                h2{\n                    color: rgb(var(--text-color));\n                }\n                }\n            }\n        }\n    }\n    .toolbar {\n        background-color: #B2E2F2;\n    }\n\n    svg.inlined-svg {\n        color: rgb(var(--text-color));\n        border-color:transparent;\n\n        circle[stroke],\n        rect[stroke],\n        line[stroke],\n        polygon[stroke],\n        ellipse[stroke],\n        polyline[stroke],\n        path[stroke],\n        g[stroke] {\n            stroke: rgb(var(--text-color))!important;\n        }\n\n        polygon[fill],\n        text[fill],\n        rect[fill],\n        circle[fill] {\n            fill: rgb(var(--text-color)) !important;\n        }\n    }\n\n    .image-button {\n\n        div {\n            &.highlight {\n                animation: highlight 3s infinite;\n            }\n        }\n\n        &.disabled {\n            opacity: 0.2;\n        }\n\n        &:not(.disabled) {\n            &:hover{\n                    box-shadow: var(--box-shadow-0);\n            }\n        }\n    }\n    @keyframes highlight {\n        0% {\n            color:rgb(var(--tint-color));\n            font-weight: var(--label-bold-font-weight);\n        }\n\n        50% {\n            color:rgba(var(--text-color), 0.4);\n            font-weight: var(--label-font-weight);\n        }\n\n        100% {\n            color:rgb(var(--tint-color));\n            font-weight: var(--label-bold-font-weight);\n        }\n    }\n\n}","/* https://css-tricks.com/snippets/css/a-guide-to-flexbox/ */\n\n.layout {\n    position: relative;\n    display: flex; \n    flex-direction: column;\n    flex-wrap: nowrap;\n    justify-content:space-between;\n\n    z-index: 20;\n    height: 100vh;\n    background-color: rgb(245, 248, 255);\n    color: #333;\n    border-bottom: 2px solid rgb(var(--tint-color));\n    scroll-snap-align: start;\n\n\n    .teaserContainer {\n        flex-grow: 4;\n        display: flex; \n        flex-direction: column;\n        flex-wrap: nowrap;\n        justify-content:start;\n        align-items: center;\n        align-content: center;\n        gap: 50px;\n        .teaser {\n            display: flex; \n            flex-direction: row;\n            justify-content:space-between;\n            align-items: start;\n            align-content: center;\n\n            .slogan {\n                max-width: 80%;\n                h1{\n                    font-weight: 500;\n                    font-size: calc(20px + 1vw);\n                    line-height: 1.2;        \n                    color:rgb(var(--tint-color));\n                }\n                .textblock{\n                    background-color: #ffffffe0;\n                    padding: 20px;\n                    border-radius: @borderRadiusBig;\n                    box-shadow:  var(--box-shadow-1);\n                }\n                font-weight: 300;\n                line-height: 1.2;\n                font-size: calc(8px + 1vw);\n                color: black;\n                padding-left:60px;\n            }\n        }\n        .launchArea{\n            display: flex;\n            text-align: center;\n            justify-content: center;\n            flex-direction: row;\n            max-height:20vh;\n            max-width:100%;\n            \n            .imageContainer {\n                flex: 1;\n                flex-flow: column wrap;\n                align-items: center;\n            \n                display: flex; \n                flex-direction: column;\n                flex-wrap: nowrap;\n                align-items: center;\n             \n                img{\n                    max-height:100%;\n                }\n            }\n        }\n          \n    }\n\n    .avatarContainer {\n        flex-grow: 0;\n\n        display: flex; \n        flex-direction: row;\n        flex-wrap: nowrap;\n        justify-content:space-between;\n        align-items: flex-end;\n        .avatar {\n            text-align: center;\n            font-weight: 400;\n            font-size: 16px;\n            display: flex;\n            flex-direction: column;\n            flex-wrap: nowrap;\n            align-content: flex-end;\n            justify-content: flex-end;\n            align-items: center;\n            height:15vh;\n            max-height:160px;\n            img {\n                height: 100%;\n            }\n        }\n\n        .arrow {\n            animation: mover 2s infinite alternate;\n            font-size:20px;\n            i {\n                height: 20px;\n                display: inline-block;\n                border-left: 4px solid #000;\n                border-radius: @borderRadiusSmall;\n                &.left {\n                    transform: rotate(-45deg);\n                }\n    \n                &.right {\n                    transform: rotate(45deg) translate(3px, -2px);\n                }\n            }\n        }\n    }\n\n    @keyframes mover {\n        0% { transform: translateY(0); }\n        100% { transform: translateY(-10px); }\n    }\n}",".intro {\n    background-color: white;\n    background-image: none;\n    text-align: left;\n    position: relative;\n    z-index: 20;\n    height: 100vh;\n    padding-bottom: 20px;\n    color: #333;\n\n    .header {\n        font-size: calc(19px + 1vw);\n        line-height: 1.2;\n        top: 2vw;\n        padding-left: 50px;\n        color:rgb(var(--tint-color));\n        position: absolute;\n        padding-top: 30px;\n        background-color: rgba(255, 255, 255, 0.8);\n        div {\n            font-size: calc(6px + 1vw);\n            font-weight: 200;\n            color: black;\n            padding-top: 30px;\n        }\n    }\n\n    img {\n        width: 50%;\n        vertical-align: middle;\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        margin: auto;\n        left: 0;\n        right: 0;\n    }\n\n    .launch_button {\n        position: absolute;\n        right: 50px;\n        bottom: 100px;\n    }\n}",".slidedeck {\n    position: absolute;\n    height: 400vh;\n    width: 100%;\n    background-image: linear-gradient(135deg, #e13f4d, #d34657);\n\n    .pink_bg {\n        position: absolute;\n        top: 100vh;\n        height: 300vh;\n        width: 100%;\n     